import {createComponent} from 'react-fela'
import Base from './Base.fela'

const Text = (props) => ({
  display: (props.inline) ? 'inline-block' : 'block',
  fontFamily: (props.font) ? props.theme[props.font] : props.theme.fonts.text,
  fontSize: (props.size) ? props.theme[props.size] : props.theme.size.text,
  color: props.theme.colors.text,

  extend: [{
    condition: props.ellipsis,
    style: {
      textOverflow: 'ellipsis',
      overflow: 'hidden',
      whiteSpace: 'nowrap',
    },
  }],
})

export default createComponent(Text, Base)



